import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react'
import './NoiseEvent.css';
import NoiseEvent from './NoiseEvent.js'
import { faSmileBeam } from '@fortawesome/free-solid-svg-icons'

function NoiseEventList(props) {
  const [noiseEvents, setNoiseEvents] = React.useState();

  const noiseEventsRef =  props.firebase.database().ref("/NoiseEvents");

  noiseEventsRef.get().then(snap => setNoiseEvents(snap.val()));

  const noiseEventList = noiseEvents?.map((noiseEvent) => {
    console.log(noiseEvent);
    <NoiseEvent/>
  });

  function addTestData() {
    noiseEventsRef.push({
      DurationMinutes: 1,
      DurationSeconds: 6,
      EndTimestampMinute : 3,
      EndTimestampSecond : 56,
      StartTimestampMinute : 2,
      StartTimestampSecond: 50
    });
  }
  
  return (
    <div class="noise-event-list-container">
      {noiseEventList && noiseEventList.length > 0 
        ? noiseEventList 
        : <div style={{display: 'flex', alignItems: 'center', flexDirection: 'column', marginTop: '5rem'}}>
            <FontAwesomeIcon icon={faSmileBeam} style={{fontSize: '7rem'}}/>
            <h1>Hooray!!</h1>
            <span>No barks have happened yet</span>
          </div>
      }
      <button onClick={addTestData()}>Add Test Data</button>
    </div>
  );
}

export default NoiseEventList;
